export default {
    checkLogin (that) {
        const {user, prompt, history} = that.props;
        if (!user) {
            prompt('');
            history.push('/signIn');
        }
    },
    checkNotLogin (that) {
        const {user, prompt, history} = that.props;
        if (user) {
            prompt('');
            history.goBack('back');
        }
    }
}