import React,{ Component } from 'react';
import {connect} from 'react-redux';
import fetchPosts from '../lib/asyncAction';
import getNextData from '../common/getNextData';
import style from '../public/css/home.scss';
import PropTypes from 'prop-types';


class Main extends Component {
    constructor (props) {
        super(props);
        getNextData(this,{
            header: {
                content: '',
                show: true,
                left: {
                    content: '/',
                    src: '/signIn',
                    avatar: true
                },
                right: {
                    content: ''
                }
            },
            search: {
                show: true
            }
        })
    }
    componentWillMount () {
        const {postInit, prompt} = this.props;
        postInit();
    }
    render () {
        return (
            <ul className={style.list}>
                <Topic></Topic>
                <Topic></Topic>
            </ul>
        )
    }
}


class Topic extends Component{
    render () {
        return (
            <li className={style.topic} onClick={this.navigateTo.bind(this)}>
                <h3></h3>
                <p>
                    : kfsdf
                </p>
                <div>
                    <span>9644</span>
                    <span className={style.comment}>699</span>
                </div>
            </li>
        )
    }
    navigateTo () {
        console.log(this.context.history.push)
    }
}
Topic.contextTypes = {
    router: PropTypes.object.isRequired
};

const mapStateToProps = (state) => ({
    posts: state.posts,
    user: state.user,
    path: state.path
});
function mapDispatchToProps(dispatch, ownProps) {
    return {
        postInit () {
            dispatch(fetchPosts('get', '/postInit',{}, 'postInit'))
        },
        prompt (msg) {
            dispatch({
                type: 'prompt',
                message: msg
            })
        },
        pageChange (path) {
            dispatch({
                type: 'changePage',
                path
            })
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Main)